@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.resumeButton {
  margin-left: 2rem;
}
.cards {
  margin-top: 2%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}

.cards grid {
  width: 100%;
  height: 100%;
}

.card {
  border-style: solid;
  border-radius: 25px;
  border-color: #7510f7;
}

/* professional carrer section*/

.description {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

.description img {
  max-width: 18rem;
  margin-left: 2%;
}

.divDesc {
  width: 80%;
  margin-right: 2%;
}

.divTypo {
  height: 100%;
  margin-left: 5%;
  margin-right: 0.5%;
  background-color: #7510f7;
  border-radius: 25px;
}

.responsive {
  width: 100%;
  height: 100%;
  border-style: solid;
  border-color: #7510f7;
  border-radius: 50%;
}

@media (max-width: 800px) {
  .description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .description img {
    height: auto;
    max-width: 18rem;
    margin: 2rem;
  }
  .divDesc {
    width: 90%;
    margin-right: 1rem;
  }
  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: auto;
  }
  .resumeButton {
    margin: auto;
    padding: 1%;
  }
}

